<template>
  <div class="page trialpreson flex-v addanalysisreport">
    <el-breadcrumb separator-class="el-icon-arrow-right"
      class="my-breadcrumb headertitle">
      <el-breadcrumb-item :to="{ name: 'Analysisreport' }">分析报告</el-breadcrumb-item>
      <el-breadcrumb-item>{{$route.query.id === 'add' ? '新增' : '编辑'}}</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="main">
      <div class="add-start flex-center"
        v-if="!isActive">
        <el-form size="small"
          label-width="6em"
          style="text-align:left">
          <div class="form-name">填写信息</div>
          <el-form-item label="户籍地址："
            class="is-required"
            style="margin-bottom:36px">
            <div>
              <el-select v-model="province"
                placeholder="请选择省"
                value-key="id"
                @change="getAddress('City', $event)">
                <el-option v-for="item in arrProvince"
                  :key="item.provinceId"
                  :label="item.provinceName"
                  :value="{name: item.provinceName, id: item.provinceId}" />
              </el-select>&nbsp;&nbsp;
              <el-select v-model='city'
                value-key="id"
                placeholder="请选择市"
                @change="getAddress('County', $event)">
                <el-option v-for="item in arrCity"
                  :key='item.cityId'
                  :label="item.cityName"
                  :value="{name: item.cityName, id: item.cityId}" />
              </el-select>&nbsp;&nbsp;
              <el-select v-model='county'
                placeholder="请选择区/县"
                value-key="id"
                @change="getAddress('Town', $event)">
                <el-option v-for="item in arrCounty"
                  :key='item.countyId'
                  :label="item.countyName"
                  :value="{name: item.countyName, id: item.countyId}" />
              </el-select>
            </div>
            <div style="margin-top:12px">
              <el-select v-model='town'
                placeholder="请选择镇/乡"
                value-key="id"
                @change="getAddress('Village', $event)">
                <el-option v-for="item in arrTown"
                  :key='item.townId'
                  :label="item.townName"
                  :value="{name: item.townName, id: item.townId}" />
              </el-select>&nbsp;&nbsp;
              <el-select v-model='village'
                placeholder="请选择村/社区"
                value-key="id">
                <el-option v-for="item in arrVillage"
                  :key='item.villageId'
                  :label="item.villageName"
                  :value="{name: item.villageName, id: item.villageId}" />
              </el-select>
            </div>
          </el-form-item>
          <el-form-item label="登记日期："
            class="is-required">
            <el-date-picker v-model='dates'
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              value-format='yyyy-MM-dd'
              :picker-options="{ disabledDate: time => time > new Date() }"
              unlink-panels>
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
      <div class="content flex-v"
        v-else>
        <div class="t-header">
          <div class="item"
            :class="{'active': isActive == 1}">
            <div class="item-tit">
              <span style="width: 21px;"
                v-if="isActive == 1">
                <img src="@/assets/img/qyactive.png" />
              </span>
              <span style="width: 21px;"
                v-else>
                <img src="@/assets/img/qy.png" />
              </span>
              <!-- <i class="icon font_family icon-ziliao"></i> -->
              <p>1.前言</p>
            </div>
            <div class="item-ico">
              <i class="el-icon-more"
                style="margin-left: 10px;"></i> <i class="el-icon-arrow-right"
                style="margin-right: 0px;"></i>
            </div>
          </div>
          <div class="item"
            :class="{'active': isActive == 2}">
            <div class="item-tit">
              <span style="width: 25px;"
                v-if="isActive == 2"><img src="@/assets/img/jbxxactive.png" /></span>
              <span style="width: 25px;"
                v-else><img src="@/assets/img/jbxx.png" /></span>
              <!-- <i class="icon font_family icon-wode"></i> -->
              <p>2.基本信息</p>
            </div>
            <div class="item-ico">
              <i class="el-icon-more"
                style="margin-left: 10px;"></i> <i class="el-icon-arrow-right"
                style="margin-right: 0px;"></i>
            </div>
          </div>
          <div class="item"
            :class="{'active': isActive == 3}">
            <div class="item-tit">
              <span style="width: 25px;"
                v-if="isActive == 3"><img src="@/assets/img/tjycactive.png" /></span>
              <span style="width: 25px;"
                v-else><img src="@/assets/img/tjyc.png" /></span>
              <!-- <i class="icon font_family icon-qunzu"></i> -->
              <p>3.体检异常汇总</p>
            </div>
            <div class="item-ico">
              <i class="el-icon-more"
                style="margin-left: 10px;"></i> <i class="el-icon-arrow-right"
                style="margin-right: 0px;"></i>
            </div>
          </div>
          <div class="item"
            :class="{'active': isActive == 4}">
            <div class="item-tit">
              <span style="width: 25px;"
                v-if="isActive == 4"><img src="@/assets/img/lndbactive.png" /></span>
              <span style="width: 25px;"
                v-else><img src="@/assets/img/lndb.png" /></span>
              <!-- <i class="icon font_family icon-qunzu"></i> -->
              <p>4.异常值历年对比</p>
            </div>
            <div class="item-ico">
              <i class="el-icon-more"
                style="margin-left: 10px;"></i> <i class="el-icon-arrow-right"
                style="margin-right: 0px;"></i>
            </div>
          </div>
          <div class="item"
            :class="{'active': isActive == 5}">
            <div class="item-tit">
              <span style="width: 25px;"
                v-if="isActive == 5"><img src="@/assets/img/wjmdactive.png" /></span>
              <span style="width: 25px;"
                v-else><img src="@/assets/img/wjmd.png" /></span>
              <!-- <i class="icon font_family icon-qunzu"></i> -->
              <p>4.未检名单</p>
            </div>
            <div class="item-ico">
              <i class="el-icon-more"
                style="margin-left: 10px;"></i> <i class="el-icon-arrow-right"
                style="margin-right: 0px;"></i>
            </div>
          </div>
          <div class="item"
            :class="{'active': isActive == 6}">
            <div class="item-tit">
              <span style="width: 25px;"
                v-if="isActive == 6"><img src="@/assets/img/jsyactive.png" /></span>
              <span style="width: 25px;"
                v-else><img src="@/assets/img/jsy.png" /></span>
              <!-- <i class="icon font_family icon-qunzu"></i> -->
              <p>5.结束语</p>
            </div>
          </div>
        </div>
        <div class="t-container ">
          <div class="item"
            style="padding-top:50px"
            v-show="isActive == 1">
            <quill-editor v-model="forewordContent"
              style="height:240px"></quill-editor>
          </div>
          <div class="item"
            style="padding-left:50px"
            v-show="isActive == 2">
            <div class="itemcontent">
              <div class="itemtitle">体检基本信息</div>
              <div class="itemmain">
                <table class="jbxxtable"
                  border="1">
                  <tr>
                    <td class="backcolor">开始时间</td>
                    <td>{{oData.registerStartDate | formatDate}}</td>
                    <td class="backcolor">截止日期</td>
                    <td>{{oData.registerEndDate | formatDate}}</td>
                  </tr>
                  <tr>
                    <td class="backcolor">体检人数</td>
                    <td>{{oData.examNum}}</td>
                    <td class="backcolor">体检批次</td>
                    <td>{{oData.batch | emptyData}}</td>
                  </tr>
                  <tr>
                    <td class="backcolor">男性参检人数</td>
                    <td>{{oData.maleNum}}</td>
                    <td class="backcolor">女性参检人数</td>
                    <td>{{oData.femaleNum}}</td>
                  </tr>
                  <tr>
                    <td class="backcolor">男性参检比例</td>
                    <td>{{oData.maleRatio | calcPercentage}}</td>
                    <td class="backcolor">女性参检比例</td>
                    <td>{{oData.femaleRatio | calcPercentage}}</td>
                  </tr>
                  <tr>
                    <td class="backcolor">主要项目</td>
                    <td colspan="3">{{oData.projects}}</td>
                  </tr>
                </table>
              </div>
            </div>
            <div class="itemcontent">
              <div class="itemtitle">重点人群分布图</div>
              <div class="itemmain">
                <div>
                  <div class="title">统计{{year}}年分析数据</div>
                  <echart-bar v-if="crowdLabel.length"
                    :xAxisDate="crowdLabel"
                    width='900px'
                    :seriesData="crowdValue"
                    title="重点人群分布图"></echart-bar>
                </div>
              </div>
            </div>
          </div>
          <div class="item"
            v-show="isActive == 3">
            <div class="itemcontent">
              <div class="itemtitle">异常人员清单</div>
              <div class="itemmain table-box">
                <el-table stripe
                  ref="multipleTable"
                  :data="anomalyUsers"
                  tooltip-effect="dark"
                  header-cell-class-name='custom-table-cell'>
                  <el-table-column type="index"
                    label="序号">
                  </el-table-column>
                  <el-table-column align="center"
                    prop="userName"
                    label="体检姓名"
                    show-overflow-tooltip>
                    <!-- USERNAME -->
                  </el-table-column>
                  <el-table-column align="center"
                    prop="examUserId"
                    label="体检登记流水号"
                    show-overflow-tooltip>
                    <!-- EXAMUSERID -->
                  </el-table-column>
                  <el-table-column align="center"
                    prop="checkConclusion"
                    label="体检结论"
                    show-overflow-tooltip>
                    <!-- CHECKCONCLUSION -->
                  </el-table-column>
                  <el-table-column align="center"
                    prop="checkProposal"
                    label="建议"
                    show-overflow-tooltip>
                    <!-- CHECKPROPOSAL -->
                  </el-table-column>
                </el-table>
              </div>
            </div>
            <div class="itemcontent">
              <div class="itemtitle">异常数据分布图</div>
              <div class="itemmain">
                <div>
                  <div class="title">统计{{year}}年分析数据</div>
                  <echart-bar v-if="anomalyValue.length"
                    :xAxisDate="anomalyLabel"
                    width="900px"
                    :seriesData="anomalyValue"
                    title="异常数据分布图" />
                  <div v-else
                    class="flex-center flex-v-center"
                    style="height:300px;color:#909399">
                    暂无数据
                  </div>
                </div>
              </div>
            </div>
            <div class="itemcontent">
              <div class="itemtitle">温馨提示</div>
              <div class="itemmain">
                <div style="width:800px">
                  <el-input type="textarea"
                    :rows="3"
                    placeholder="请输入内容"
                    v-model="warmTip"></el-input>
                </div>
              </div>
            </div>
          </div>
          <div class="item"
            v-show="isActive == 4">
            <div class="itemcontent">
              <div class="itemtitle">异常值历年对比</div>
              <div class="itemmain table-box">
                <el-table stripe
                  ref="multipleTable"
                  :data="contrastAnomalys"
                  tooltip-effect="dark"
                  header-cell-class-name='custom-table-cell'>
                  <el-table-column type="index"
                    width="50"
                    label="序号">
                  </el-table-column>
                  <el-table-column align="center"
                    prop="CHECKCONCLUSION"
                    label="体检结论"
                    show-overflow-tooltip>
                  </el-table-column>
                  <el-table-column align="center"
                    prop="LASTVALUE"
                    label="上次"
                    width="120">
                    <template v-slot='scope'>
                      {{scope.row.LASTVALUE | emptyData}}
                    </template>
                  </el-table-column>
                  <el-table-column align="center"
                    prop="THISVALUE"
                    label="本次">
                    <template v-slot='scope'>
                      {{scope.row.THISVALUE | emptyData}}
                    </template>
                  </el-table-column>
                  <el-table-column align="center"
                    prop="advice"
                    label="对比结果">
                    <template v-slot='scope'>
                      <i class="el-icon-bottom"
                        style="font-size:20px;color:#5AD4BB"
                        v-show="compare(scope.row)===-1"></i>
                      <i class="el-icon-top"
                        style="font-size:20px;color:#FE6F71"
                        v-show="compare(scope.row)===1"></i>
                    </template>
                  </el-table-column>
                  <el-table-column align="center"
                    prop="RULE"
                    label="范围">
                  </el-table-column>
                </el-table>
              </div>
            </div>
          </div>
          <div class="item"
            v-show="isActive == 5">
            <div class="itemcontent">
              <div class="itemtitle">未检名单</div>
              <div class="itemmain table-box">
                <el-table stripe
                  ref="multipleTable"
                  :data="nocheckNames"
                  tooltip-effect="dark"
                  header-cell-class-name='custom-table-cell'
                  class="test-box">
                  <el-table-column type="index"
                    label="序号">
                  </el-table-column>
                  <el-table-column align="center"
                    prop="USERNAME"
                    label="姓名"
                    width="200">
                  </el-table-column>
                  <el-table-column align="center"
                    prop="SEXTYPE"
                    label="性别"
                    width="120">
                    <template v-slot='scope'>
                      {{
                        scope.row.SEXTYPE == 1 
                        ? '男' : scope.row.SEXTYPE == 2
                        ? '女' : '不详'
                      }}
                    </template>
                  </el-table-column>
                  <el-table-column align="center"
                    prop="AGE"
                    label="年龄"
                    width="60">
                  </el-table-column>
                  <el-table-column align="center"
                    prop="RESIDENCEADDRESS"
                    label="户籍地址"
                    show-overflow-tooltip>
                  </el-table-column>
                  <el-table-column align="center"
                    prop="CONTACTPHONE"
                    label="联系电话"
                    width="150">
                  </el-table-column>
                </el-table>
              </div>
            </div>
          </div>
          <div class="item"
            style="padding-top:50px"
            v-show="isActive == 6">
            <quill-editor v-model="tagContent"
              style="height:240px"></quill-editor>
          </div>
        </div>
      </div>
    </div>
    <div class="footer">
      <el-button class="btn-hollowing"
        v-show="!isActive">取消</el-button>
      <el-button class="btn-hollowing"
        v-show="isActive>1"
        @click="prevStep()">上一步</el-button>
      <el-button class="btn-info"
        v-show="isActive<6"
        :loading="submitLoading"
        @click="nextStep()">下一步</el-button>
      <el-button class="btn-info"
        v-show="isActive===6"
        :loading="submitLoading"
        @click="submitSave">保存</el-button>

    </div>
  </div>
</template> 
<script>
import { quillEditor } from 'vue-quill-editor' //调用编辑器
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import EchartBar from '@/components/echart/echartbarzhu'
import { formatDate } from '@/lib/utils'

// 省 市 区 镇 村 键名
const ADDRESS_KEYS = ['Province', 'City', 'County', 'Town', 'Village']

// 人群字典
const CROWD = [
  // { name: '中医药', field: 'tcmNum', value: 0 },
  // { name: '传染病', field: 'contagionNum', value: 0 },
  // { name: '慢病管理', field: 'chronicDiseaseNum', value: 0 },
  // { name: '贫困人口', field: 'poorPeopleNum', value: 0 },
  // { name: '预防接种', field: 'vaccinateNum', value: 0 }
  { name: '高血压', field: 'hypertensionNum', value: 0 },
  { name: '2型糖尿病', field: 'diabetesNum', value: 0 },
  { name: '老年人', field: 'oldNum', value: 0 },
  { name: '孕产妇', field: 'pregnantNum', value: 0 },
  { name: '肺结核患者', field: 'phthisisNum', value: 0 },
  { name: '严重精神障碍患者', field: 'psychosisNum', value: 0 },
  { name: '0~6岁儿童', field: 'childrenNum', value: 0 }
]

export default {
  name: 'addanalysisreport',
  components: {
    quillEditor,
    EchartBar
  },
  data() {
    return {
      isActive: 0,
      oData: {},

      // 异常人员清单
      anomalyUsers: [],

      // 异常数据
      // anomalyDistributes: [],

      // 异常值历年对比
      contrastAnomalys: [],

      // 未检名单
      nocheckNames: [],

      // 重点人群图表数据
      crowdLabel: [],
      crowdValue: [],

      // 异常图表数据(疾病)
      anomalyLabel: [],
      anomalyValue: [],

      //
      arrProvince: [], // 省 getProvince
      arrCity: [], // 市 getCity
      arrCounty: [], // 县 getCounty
      arrTown: [], // 镇 getTown
      arrVillage: [], // 村 getVillage

      province: '',
      city: '',
      county: '',
      town: '',
      village: '',

      // 提交字段
      dates: '',
      forewordContent: '', //　前言
      warmTip: '', // 温馨提示
      tagContent: '', // 结束语
      submitLoading: false
    }
  },

  created() {
    this.initData()
  },

  methods: {
    initData() {
      let { id } = this.$route.query
      if (id === 'add') {
        this.getAddress('Province')
      } else {
        this.isActive = 1
        this.getReport(id)
      }
    },

    // 查询和新增分析报告接口
    handleReport(handle) {
      let { province, city, county, town, village, dates } = this
      let residenceAddress = `
        ${province.name}${city.name}${county.name}${town.name}${village.name}
      `
      return this.$post('/analyzereport/getOrAddAnalyzeReport', {
        registerStartDate: dates ? `${dates[0]} 00:00:00` : '',
        registerEndDate: dates ? `${dates[1]} 23:59:59` : '',
        handle,
        foreword: this.forewordContent, // 前言
        batch: '无',
        tag: this.tagContent,
        warmTip: this.warmTip,
        residenceAddress: residenceAddress
        // 广东省深圳市宝安区松岗街道碧头社区
      })
    },

    // 排序重点人群
    sortCrowd(data) {
      let crowdLabel = []
      let crowdValue = []
      let crowd = CROWD.map(item => {
        item.value = data[item.field] || 0
        return item
      })
        .sort((it, next) => next.value - it.value)
        .forEach(t => {
          crowdLabel.push(t.name)
          crowdValue.push(t.value)
        })
      this.crowdValue = crowdValue
      this.crowdLabel = crowdLabel
    },

    // 处理数据
    handleData(data) {
      this.oData = data
      this.forewordContent = data.foreword || ''
      this.tagContent = data.tag || ''
      this.warmTip = data.warmTip || ''
      this.sortCrowd(data)
      this.anomalyUsers = JSON.parse(data.anomalyUser)
      let anomalyDistributes = JSON.parse(data.anomalyDistribute)
      this.contrastAnomalys = JSON.parse(data.contrastAnomaly)
      this.nocheckNames = JSON.parse(data.nocheckName)
      anomalyDistributes = anomalyDistributes.sort((it, next) => next.TOTALCOUNT - it.TOTALCOUNT)
      this.anomalyLabel = anomalyDistributes.map(item => item.CHECKPROPOSAL) //CHECKPROPOSAL
      this.anomalyValue = anomalyDistributes.map(item => item.TOTALCOUNT)
    },

    // 比较上次和本次的异常值
    compare({ THISVALUE, LASTVALUE }) {
      if (!THISVALUE || !LASTVALUE) return 0
      let thisVal = Number(THISVALUE)
      let lastVal = Number(LASTVALUE)
      return thisVal === lastVal ? 0 : thisVal > lastVal ? 1 : -1
    },

    getReport(reportExamId) {
      this.$post('/analyzereport/getAnalyzeReportById', {
        reportExamId
      })
        .then(({ data }) => {
          this.handleData(data)
        })
        .catch(err => err && this.$message.error(err))
    },

    // 获取地址（省、市、区、镇、村）
    getAddress(type, val) {
      let i = ADDRESS_KEYS.indexOf(type)
      let params = {}
      if (i > 0) {
        let key = ADDRESS_KEYS[i - 1].toLocaleLowerCase()
        params[`${key}Id`] = val.id
        for (let j = i, t; (t = ADDRESS_KEYS[j++]); ) {
          this[t.toLocaleLowerCase()] = ''
          this[`arr${t}`] = []
        }
      }
      this.$post(`/dict/get${type}`, params)
        .then(({ data }) => {
          this[`arr${type}`] = data
        })
        .catch(err => err && this.$message.error(err))
    },

    prevStep() {
      this.isActive--
    },
    nextStep() {
      if (!this.isActive) {
        if (!this.village) return this.$message.warning('户籍地址每一项为必选！')
        if (!this.dates) {
          return this.$message.warning('请选择登记日期！')
        }
        this.submitLoading = true
        this.handleReport('get')
          .then(({ data }) => {
            this.submitLoading = false
            this.handleData(data)
            this.isActive++
          })
          .catch(err => {
            this.submitLoading = false
            this.$message.error(err)
          })
        return
      }
      if (this.isActive === 1 && this.forewordContent.trim() === '') {
        return this.$message.warning('请输入前言！')
      }
      if (this.isActive === 3 && this.warmTip.trim() === '') {
        return this.$message.warning('请输入温馨提示！')
      }
      this.isActive++
    },

    submitSave() {
      if (this.tagContent.trim() === '') {
        return this.$message.warning('请输入结束语！')
      }
      this.submitLoading = true
      this.handleReport('add')
        .then(res => {
          this.submitLoading = false
          this.$message.success('保存成功')
          this.$router.push({ name: 'Analysisreport' })
        })
        .catch(err => {
          this.submitLoading = false
          this.$message.error(err)
        })
    }
  },
  filters: {
    calcPercentage(num) {
      if (!num) return 0
      let n = (num * 100).toFixed(1)
      return `${n}%`
    }
  },
  computed: {
    year() {
      let start = formatDate(this.oData.registerStartDate) || ''
      let end = formatDate(this.oData.registerEndDate) || ''
      start = start.slice(0, 4)
      end = end.slice(0, 4)
      if (start === end) return start
      return `${start}至${end}`
    }
  }
}
</script>
<style lang="less" scoped>
@import '../../styles/FirstTrial/fristtrial.less';
.addanalysisreport {
  .marginright20 {
    margin-right: 20px;
  }
  .main {
    position: relative;
    flex: 1;
    // margin: 0 30px;
    text-align: center;
    overflow: hidden;
    // height: 143px;
    .content {
      text-align: left;
